import React from "react";
import {Link, useLocation, useHistory} from "react-router-dom";
import logo from "../assets/logo.png";

const Navbar = () => {
  let history = useHistory();
  let location = useLocation();

  const handleLogout = () => {
    localStorage.removeItem("token");
    history.push("/");
  };

  return (
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
      <div className="container-fluid">
        <Link
          className="navbar-brand"
          to={!localStorage.getItem("token") ? `/` : "/"}
          style={{color:"#FFFFF0",fontWeight:"bold"}}
        >
          <img
            src={logo}
            alt=""
            width="30"
            height="30"
            className="mx-2 d-inline-block align-text-top"

          />
          iNotebook
        </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <Link
                className={`nav-link ${
                  location.pathname === "/home" ? "active" : ""
                }`}
                aria-current="page"
                to={!localStorage.getItem("token") ? `/` : "/home"}
                style={{color:"#20c997"}}
              >
                Home
              </Link>
            </li>
            <li className="nav-item">
              <Link
                className={`nav-link ${
                  location.pathname === "/about" ? "active" : ""
                }`}
                to="/about"
                style={{color:"#20c997"}}
              >
                About
              </Link>
            </li>
          </ul>
          {!localStorage.getItem("token") ? (
            <form className="d-flex">
              <Link className="btn  mx-1" to="/login" role="button" style={{color:"#20c997"}}>
                Login
              </Link>
              <Link className="btn mx-1" to="/signup" role="button" style={{color:"#FFFFF0"}}>
                Sign Up
              </Link>
            </form>
          ) : (
            <div className="d-flex">
              <button onClick={handleLogout} className="btn" style={{fontWeight:"bold", color: "white",backgroundColor:"#20c997" }}>
                Log Out
              </button>
            </div>
          )}
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
